$pad:992px;
$mobile: 768px;


       @mixin pad {
            @media(max-width:$pad){
                   @content;
                    }
                  }
       @mixin mobile {
           @media(max-width:$mobile){
                  @content;
                    }
                   }
                    
.container{
       max-width: 1110px;
       padding: 15px;
       margin: 0 auto;
          @include pad{
              max-width: 95%;
                      }
          @include mobile{
              max-width: 100%;
                     } 
}
